import React from 'react';
import { useHistory } from 'react-router-dom';
import "./style/Logout.css";

const Logout = () => {
  const history = useHistory();

  const handleLogout = async () => {
    // Xa d liu email, fullname v phonenumber khi sessionStorage
    sessionStorage.removeItem('email');
    sessionStorage.removeItem('fullName');
    sessionStorage.removeItem('phone_number');

    // Xa d liu ng nhp khi sessionStorage hoc localStorage
    sessionStorage.removeItem('token');
    localStorage.removeItem('token');

    // iu hng ngi dng n trang ng nhp hoc trang chnh ca ng dng
    history.push('/');
  };

  return (
    <div className='logout'>
      <h1>ng xut</h1>
      <p>Bn c chc chn mun ng xut?</p>
      <button className='btn btn-primary' onClick={handleLogout}>ng xut</button>
    </div>
  );
};

export default Logout;
